import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
	  // mode: 'history',
routes: [
	   	{path:'/', redirect:'/home'},
	   	{path:'*', redirect:'/home'},
	   	{path:'/home',name:'首页',meta:{keepAlive: false},component:resolve =>{require(['@/components/home'],resolve)}}
	]
})


