<template>
	<div id="app">
		 <!-- <transition :name="transitionName">
			<keep-alive>
				<router-view class="Router"></router-view>
			</keep-alive>
		</transition> -->
		<transition :name="transitionName">
				<keep-alive>
				    <router-view v-if="$route.meta.keepAlive" class="Router">
				    </router-view>
				</keep-alive>
		</transition>
		
		<transition :name="transitionName">
				<router-view v-if="!$route.meta.keepAlive" class="Router"></router-view>
		</transition>
	</div>
</template>

<script>
	export default {
		//name: 'App'
		data() {
			return {
				transitionName: 'slide-right', // 默认动态路由变化为slide-right
				pto: [],
				pfrom: [],
				temp: 0
			}
		},
		watch: {
			'$route'(to, from) {
				//let isBack = this.$router.isBack  //  监听路由变化时的状态为前进还是后退
				// console.log(isBack)
				//console.log(this.from.path);
				if (this.temp == 0) {
					this.transitionName = 'slide-left'
					this.pfrom.push(from.path);
					this.temp = 1;
					return;
				} else if (from.path == this.pfrom[0]) {
					this.$router.push(this.pfrom[0]);
					//return;
				} else if (to.path != this.pfrom[this.pfrom.length - 1]) {
					this.transitionName = 'slide-left';
					this.pfrom.push(from.path);
					//return;
				} else if (to.path == this.pfrom[this.pfrom.length - 1]) {
					this.transitionName = 'slide-right';
					this.pfrom.pop(from.path);
					//return;
				}

			}
		}
	}
</script>

<style lang="scss">
	::-webkit-scrollbar {
		display: none;
	}

	#app {
		-webkit-text-size-adjust: 100%;
		font-family: "PingFang SC";
		width: 100%;
		height: 100%;
		font-size: .27rem;
	}

	.Router {
		position: absolute;
		width: 100%;
		height: 100%;
		transition: all .5s ease;
	}

	.slide-left-enter,
	.slide-right-leave-active {
		opacity: 0;
		-webkit-transform: translate(100%, 0);
		transform: translate(100%, 0);
	}

	.slide-left-leave-active,
	.slide-right-enter {
		opacity: 0;
		-webkit-transform: translate(-100%, 0);
		transform: translate(-100% 0);
	}

	[v-clock] {
		display: none;
	}
</style>



